@font-face {
    font-family: 'Jawwy';
    font-weight: 100;
    src: url(assets/fonts/Jawwy-Thin.woff2) format('woff2'),
    url(assets/fonts/Jawwy-Thin.woff) format('woff'),
    url(assets/fonts/Jawwy-Thin.ttf)  format('truetype');
}

/*  Accord Alternate ExtraLight + Tanseek Modern Pro Arabic Light */
@font-face {
    font-family: 'Jawwy';
    font-weight: 200;
    src: url(assets/fonts/Jawwy-ExtraLight.woff2) format('woff2'),
    url(assets/fonts/Jawwy-ExtraLight.woff) format('woff'),
    url(assets/fonts/Jawwy-ExtraLight.ttf)  format('truetype');
}

